.review_detail_container {
  z-index: 999;
  position: fixed;
  width: 100%;
  height: 100%;
  align-self: flex-start;
  left: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.6);
}
.review_detail_content {
  background-color: #e0f7fa;
  width: 25rem;
  padding: 1.5rem;
  border-radius: 0.8rem;
  height: auto;
  max-height: 25rem;
  box-sizing: border-box;
  overflow: hidden;
  cursor: default;
}
#ioClose {
  color: red;
  cursor: pointer;
  font-size: 2.5rem;
}
.review_detail_header {
  display: flex;
  justify-content: space-between;
}
.review_detail_title,
.review_detail_close {
  display: flex;
  align-items: center;
}
.review_detail_title {
  font-size: 1.9rem;
}
.reviews_detail_text {
  overflow-y: scroll;
  height: auto;
  max-height: 15rem;
  padding-top: 0.6rem;
}
.review_detail_name {
  font-weight: 700;
  font-size: 1.1rem;
}
.review_detail_header {
  padding-bottom: 1rem;
}
.review_detail_rating {
  display: flex;
}
.review_detail_stars {
  font-size: 1.4rem;
}
@media (max-width: 800px) {
  .review_detail_title {
    font-size: 1.5rem;
  }
  .review_detail_stars {
    font-size: 1.3rem;
  }
  .reviews_detail_text {
    font-size: 0.85rem;
  }
  .review_detail_name {
    font-weight: 600;
    font-size: 0.99rem;
  }
}
