.modal-win-for-order-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-win-for-order-section {
  background: white;
  padding: 20px;
  border-radius: 8px;
  position: relative;
  max-width: 90%;
  width: 500px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  box-sizing: border-box;
}

.modal-win-for-order-title-for-order {
  font-size: 24px;
  margin: 0;
}

.modal-close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background: transparent;
  border: none;
  font-size: 18px;
  cursor: pointer;
  background-color: #007bff;
  color: #fff;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.modal-close-button:hover {
  background-color: #0056b3;
}

.modal-win-for-order-text-info-main {
  border-top: 1px solid black;
  border-bottom: 1px solid black;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
}

.modal-win-for-order-text-info {
  line-height: 1.5;
}

.modal-win-for-order-text-info h3 {
  font-weight: 700;
  font-size: 16px;
  margin: 0;
}

.modal-win-for-order-text-info h4 {
  font-weight: 400;
  font-size: 14px;
  margin: 0;
}

.modal-win-for-order-text-info-price span {
  font-weight: 300;
}

.modal-win-for-order-inputs {
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.the-chosen-car-for-order-in-modal-window {
  margin: 18px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.the-chosen-car-for-order-in-modal-window h3 {
  font-weight: 400;
  font-size: 15px;
  margin: 0;
}

.the-chosen-car-for-order-in-modal-window span {
  font-size: 15px;
  font-weight: 700;
}

.modal-win-for-order-inputs-itself {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  outline: none;
  height: 40px;
  border-radius: 10px;
  font-size: 15px;
  font-family: Comfortaa, sans-serif;
  border: none;
  background-color: #f3f5f9;
  box-sizing: border-box;
}

.modal-win-for-order-inputs-itself.for-addition-info {
  height: 75px;
  resize: vertical;
}

.modal-win-for-order-text-info-more-then-one-times {
  text-align: right;
  font-size: 15px;
}

.modal-win-for-order-btn {
  margin: 10px 0 0 0;
  padding: 15px;
  width: 100%;
  max-width: 250px;
  border: none;
  border-radius: 10px;
  font-family: Comfortaa, sans-serif;
  background-color: #007bff;
  color: #ffff;
  font-size: 17px;
  transition: background-color 0.3s;
  box-sizing: border-box;
}

.modal-win-for-order-btn:hover {
  background-color: #0056b3;
}

/* Мобильные стили */
@media (max-width: 600px) {
  .modal-win-for-order-section {
    width: 90%;
    max-width: none;
  }

  .modal-win-for-order-title-for-order {
    font-size: 20px;
  }

  .modal-win-for-order-inputs-itself {
    font-size: 14px;
  }

  .modal-win-for-order-btn {
    font-size: 15px;
    padding: 10px;
  }
}

