/* выбор машины модальное окно */
/* Блокировка заднего фона */
.custom-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5); /* Затемнение фона */
  z-index: 1000;
  display: flex;
  justify-content: center;
  align-items: center;
}

.custom-modal-window {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  padding: 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  z-index: 1001;
  max-width: 500px;
  width: min-content;
  max-height: 80vh;
  overflow: hidden;
  border-radius: 10px;
}

.custom-modal-window h2 {
  margin-top: 0;
}

.custom-car-scroll-container {
  height: 60vh;
  overflow-y: auto;
  margin-bottom: 20px;
}

.custom-car-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.custom-car-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid #ddd;
  padding: 15px;
  margin-bottom: 15px;
  min-height: 150px;
  box-sizing: border-box;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Стили для контейнера слайдера */
.slider-container {
  position: relative;
  width: 100%;
  overflow: hidden;
}

/* Стили для слайдера */
.slider-container .slick-slide {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Стили для изображений */
.custom-car-photo {
  width: auto;
  height: 320px;
  object-fit: contain;
  border-radius: 8px;
}
.slider-container {
  height: 380px;
}

.swiper-backface-hidden .swiper-slide {
  width: fit-content !important;
}
.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}

.custom-car-details {
  flex-grow: 1;
  text-align: left;
}

.custom-car-details h3 {
  font-size: 23px;
}
.custom-car-details p {
  font-size: 16px;
}

.custom-select-button {
  background-color: #4caf50;
  color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 4px;
  font-family: Comfortaa;
  transition: 0.3s ease;
}

.custom-select-button:hover {
  background-color: #45a049;
}

.custom-close-button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  border-radius: 4px;
  transition: 0.3s ease;
  font-family: Comfortaa;
  font-size: 14px;
}

.custom-close-button:hover {
  background-color: #0056b3;
}

.no-scroll {
  overflow: hidden;
}

@media all and (max-width: 601px) {
  .custom-modal-window {
    width: 88%;
  }
  .custom-car-photo {
    width: auto;
    height: 220px;
  }
  .slider-container {
    height: 280px;
  }
  .custom-modal-window {
    padding: 15px;
  }
}

/* выбор машины модальное окно */

/* сообщение о том, что нужно выбрать машину */
.notification {
  position: fixed;
  top: 20px;
  right: 20px;
  background-color: #ff4444;
  color: white;
  padding: 15px;
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  visibility: hidden;
  opacity: 0;
  animation: none;
  transition: opacity 0.5s ease-in-out;
  z-index: 7;
}
.notificationGreen {
  position: fixed;
  top: 20px;
  right: 20px;
  background-color: #4caf50;
  color: white;
  padding: 15px;
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  visibility: hidden;
  opacity: 0;
  animation: none;
  transition: opacity 0.5s ease-in-out;
  z-index: 7;
}

.notificationGreen.show {
  visibility: visible;
  opacity: 1;
  animation: slideIn 0.3s ease-out forwards;
}
.notification.show {
  visibility: visible;
  opacity: 1;
  animation: slideIn 0.3s ease-out forwards;
}

@keyframes slideIn {
  0% {
    transform: translateY(100%);
    opacity: 0;
  }
  100% {
    transform: translateX(0);
    opacity: 1;
  }
}

