.navbar {
  animation: slideInTop 1s ease-out;
  width: 100%;
  user-select: none;
}
.navbar .container {
  padding: 0 100px;
  background: #202846;
  height: 90px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-transform: uppercase;
  flex-wrap: wrap;
}

@keyframes slideInTop {
  from {
    transform: translateY(-100%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.main-text-links {
  text-decoration: none;
  color: white;
  font-weight: bold;
  font-size: 18px;
  position: relative; /* Необходимо для корректного позиционирования псевдоэлемента */
}

.main-text-links.active::after {
  content: "";
  display: block;
  width: 100%;
  height: 2px;
  background: #fff;
  position: absolute;
  bottom: -2;
  left: 0;
  animation: slideIn 0.3s ease forwards;
}

@keyframes slideIn {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

.nav-link {
  text-decoration: none;
  height: 19px;
  padding: 10px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  cursor: pointer;
  border-radius: 15px;
  font-weight: bold;
  font-size: 18px;
  transition: 0.3s ease;
}

.tours-link,
.group-tours-link {
  color: white;
}

.tours-icon,
.group-tours-icon {
  color: white;
}

.nav-drop-down-main {
  position: relative;
  display: flex;
  justify-content: center;
}

.nav-main-text {
  display: flex;
  align-items: center;
  gap: 50px;
  flex-wrap: wrap;
  z-index: 5;
}

.nav-logo {
  height: 60px;
}

/* Создание burgerMenu время 2 часа ночи я скоро умру */
.header__burger-btn {
  display: none;
  position: relative;
  width: 40px;
  height: 40px;
  border: none;
  background-color: transparent;
  z-index: 6;
}
.header__burger-btn span {
  position: absolute;
  width: 30px;
  height: 3px;
  background-color: black;
  left: 5px;
  transition: transform 0.5s, opacity 0.5s, background-color 0.5s;
}
.header__burger-btn span:nth-child(1) {
  transform: translateY(-10px);
}

.header__burger-btn span:nth-child(3) {
  transform: translateY(10px);
}
.header__burger-btn span {
  background-color: white;
}
.navbar.open .header__burger-btn span:nth-child(1) {
  transform: translateY(0) rotate(45deg);
}

.navbar.open .header__burger-btn span:nth-child(2) {
  opacity: 0;
}

.navbar.open .header__burger-btn span:nth-child(3) {
  transform: translateY(0) rotate(-45deg);
}

.part-for-burger-menu {
  display: flex;
  align-items: center;
  gap: 10px;
  display: none;
}
.profile-picture-for-navbar {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.burger-menu-author-text {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.burger-menu-author-text span:nth-child(1) {
  font-size: 13px;
}
.part-for-burger-menu span {
  font-size: 11px;
  color: #fff;
}

/* Создание burgerMenu время 2 часа ночи я скоро умру */

.auth-part-of-wite-main.for-bg-menu {
  display: none;
  align-items: center;
  gap: 0.5rem;
}
/*  */
/* auth-part */
.btn-lan-switcher-and-auth-part {
  display: flex;
  align-items: center;
  gap: 10px;
}
.auth-part-of-wite-main {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.auth-part-of-wite-main-icon {
  font-size: 45px;
  color: #fff;
}
.auth-part-of-wite-main span {
  font-size: 14px;
  color: #fff;
}

/* dropdown */
.drop-down-content-auth {
  position: absolute;
  top: 110%;
  background-color: white;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  list-style: none;
  padding: 0;
  margin: 0;
  width: max-content;
  z-index: 1000;
  border-radius: 10px;
  padding: 6px;
}
.drop-down-content-auth .drop-down-content-auth-exit {
  color: #000;
  font-size: 14px;
}
/* dropdown */

/* auth-part */

@media all and (max-width: 1590px) {
