    font-size: 14px;
  }
  .language-btn {
    font-size: 14px;
  }
  .nav-main-text {
    gap: 30px;
  }
  .main-text-links {
    font-size: 14px;
  }
  .nav-search-part span {
    font-size: 14px;
  }
  .nav-author-part {
    font-size: 14px;
    gap: 5px;
  }
  .person-for-auth-icon {
    font-size: 25px;
  }
  .navbar .container {
    padding: 0 30px;
  }
}
@media all and (min-height: 1366px) {
  .navbar .container {
    height: 82px;
  }
}
@media all and (max-width: 1024px) {
  .nav-search-part span {
    font-size: 11px;
  }
  .nav-search-part-icon {
    font-size: 16px;
    padding: 4px;
  }
  .nav-logo {
    height: 70%;
  }
  .nav-main-text {
    gap: 5px;
  }
  .navbar .container {
    padding: 0 50px;
  }
  .nav-author-part {
    font-size: 11px;
  }
  .nav-link {
    font-size: 13px;
  }
  .language-btn {
    font-size: 13px;
  }
  .main-text-links {
    font-size: 13px;
  }
  .auth-part-of-wite-main span,
  .drop-down-content-auth .drop-down-content-auth-exit {
    font-size: 13px;
  }
  .nav-main-text {
    gap: 13px;
  }
  .navbar .container {
    height: 82px;
  }
}
@media all and (max-width: 959px) {
  .navbar {
    padding-bottom: 80px;
  }
  .navbar .container {
    position: fixed;
    width: -webkit-fill-available;
    z-index: 5;
  }
  .header__burger-btn {
    display: block;
  }

  .auth-part-of-wite-main.stay {
    display: none !important;
  }

  .auth-part-of-wite-main.for-bg-menu {
    display: block;
    display: flex !important;
    flex-direction: row;
  }
  .auth-part-of-wite-main-icon {
    font-size: 55px;
  }
  .auth-part-of-wite-main.for-bg-menu span {
    font-size: 15px;
    font-weight: bold;
  }
  .nav-author-part {
    display: none;
  }
  .part-for-burger-menu {
    margin-top: 10px;
    display: block;
    display: flex;
  }
  .nav-main-text {
    position: absolute;
    left: -311px;
    top: 0px;
    width: 270px;
    height: auto;
    background-color: #202846;
    padding: 110px 20px;
    transition: transform 0.5s;
    align-items: center;
    flex-wrap: nowrap;
    flex-direction: column;
    align-items: start;
    gap: 50px;
  }
  .nav-logo {
    height: 65%;
    margin-left: auto;
  }
  .nav-author-part {
    font-size: 12px;
  }
  .nav-link {
    font-size: 16px;
    width: 120px;
    display: contents;
  }
  .language-btn {
    font-size: 16px;
  }
  .main-text-links {
    font-size: 16px;
  }
  .person-for-auth-icon {
    font-size: 26px;
  }
  .navbar.open .nav-main-text {
    transform: translateX(100%);
  }

  .nav-drop-down-main {
    display: block;
  }
  .drop-down-content-auth {
    position: absolute;
    top: 110%;
    background-color: transparent;
    box-shadow: none;
    list-style: none;
    padding: 0;
    margin: 0;
    width: max-content;
    z-index: 1000;
    border-radius: 10px;
    padding: 6px;
  }
  .drop-down-content-auth .drop-down-content-auth-exit {
    color: white;
    font-size: 12px;
  }
}
@media all and (max-width: 430px) {
