    position: absolute;
    left: -272px;
    top: 0px;
    width: 230px;
    background-color: #202846;
    padding: 110px 20px;
    transition: transform 0.5s;
    align-items: center;
    flex-wrap: nowrap;
    flex-direction: column;
    gap: 50px;
    align-items: start;
  }
  .nav-logo {
    height: 70%;
  }
}
