/* suka zaibalo eto */
.country-cards-wrapper {
  padding: 20px 100px;
}
.country-cards-section {
  display: flex;
  gap: 30px;
  justify-content: center;
}
.country-card-itself {
  position: relative;
  width: -webkit-fill-available;
  height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 20px;
  background-position: center;
  background-size: cover;
  color: #fff;
  border-radius: 20px;
  overflow: hidden;
  gap: 10px;
  transition: 0.5s ease;
}

.country-card-itself::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3);
  z-index: 1;
}

.country-card-itself > * {
  position: relative;
  z-index: 2;
}

.country-card-itself h3 {
  font-size: 50px;
  font-weight: bolder;
  margin: 0;
  line-height: 1.2;
}
.country-card-itself span {
  font-size: 18px;
  font-weight: 300;
  padding-top: 20px;
}
.country-card-itself button {
  font-family: Comfortaa;
  height: auto;
  padding: 15px;
  font-size: 17px;
  margin-top: 20px;
  background-color: #007bff;
  color: #fff;
  width: fit-content;
  border: none;
  border-radius: 7px;
  transition: 0.5s;
}
.country-card-itself button:hover {
  background-color: #0056b3;
}

/* kg cards */
.country-card-itself.kg {
  background-image: url(/src/Assets/img/kg-photo.jpg);
}

/* kg cards */

