.footer_container {
  display: flex;
  background-color: #e0f7fa;
  padding: 4rem;
  border-radius: 0.5rem;
  box-shadow: 0px -0px 10px rgba(0, 0, 0, 0.2);
  margin-top: 1rem;
  box-sizing: border-box;
  overflow: hidden;
}

/* .footer_logo {
 text-align: center;
} */
.footer_logo span {
  color: #0d47a1;
  font-size: 3.7rem;
  font-weight: 900;
  text-align: center;
}

.footer_logo marquee {
  padding-top: 1.7rem;
  font-weight: bold;
}

.footer_social_item {
  display: flex;
  gap: 1rem;
}

.footer_container ul {
  list-style-type: none;
  padding: 0;
}

.footer_container a {
  text-decoration: none;
  color: gray;
  transition: 0.1s;
  box-sizing: border-box;
}

.footer_container a:hover {
  color: rgb(0, 0, 146);
}

.footer_links ul {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.footer_links {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  padding-right: 1.5rem;
}

.footer_links span {
  font-weight: bolder;
}

.footer_social_item {
  padding-top: 1rem;
}
.copyNumber {
  position: absolute;
  top: 0;
  left: 0;
}
.social_media_sender {
  font-size: 35px;
}
.social_media_sender.insta:hover {
  color: #dd2a7b;
}
.social_media_sender.whats:hover {
  color: #25d366;
}
.social_media_sender.teleg:hover {
  color: #0088cc;
}
/* Media queries for responsive design */
@media (max-width: 1165px) {
  .footer_container {
    flex-direction: column;
    padding: 2rem;
  }

  .footer_logo span {
    font-size: 2.5rem;
  }

  .footer_logo marquee {
    font-size: 1rem;
    padding-top: 1rem;
  }
  .footer_container {
    padding: 1rem;
  }

  .footer_logo {
    text-align: center;
  }

  .footer_links {
    text-align: center;
  }

  .footer_social_item {
    justify-content: center;
  }
  .footer_links {
    padding-right: 0rem;
  }
}

@media (max-width: 768px) {
  .footer_container {
    padding: 1rem;
  }

  .footer_logo {
    text-align: center;
  }

  .footer_links {
    text-align: center;
  }

  .footer_social_item {
    justify-content: center;
  }
}

@media (max-width: 480px) {
  .footer_container {
    padding: 0.5rem;
  }

  .footer_logo span {
    font-size: 1.5rem;
  }

  .footer_logo marquee {
    font-size: 0.8rem;
  }
}
@media (min-width: 1800px) {
  .footer_container {
    justify-content: space-evenly;
    gap: 0;
  }
  .footer_links {
    font-size: 1.5rem;
  }
}
