.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  text-align: center;
}

.mainText {
  font-size: 3rem;
  color: #dc3545;
  margin-bottom: 20px;
}

.h2 {
  font-size: 2rem;
  margin: 0px 0px 20px 0px;
}

.p {
  color: #6c757d;
  margin: 0;
  font-size: 1.5rem;
}

.button {
  margin-top: 24px;
  color: #fff;
  background-color: #007bff;
  padding: 8px 16px;
  border-radius: 6px;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s;
  font-family: Comfortaa, sans-serif;
  width: 260px;
  height: 50px;
  font-size: 20.8px;
}

.button:hover {
  background-color: #0056b3;
}

/* Адаптивные стили */
@media (max-width: 768px) {
  .mainText {
    font-size: 2.5rem;
  }

  .h2 {
    font-size: 1.8rem;
  }

  .p {
    font-size: 1.2rem;
  }

  .button {
    width: 200px;
    height: 45px;
    font-size: 18px;
  }
}

@media (max-width: 480px) {
  .mainText {
    font-size: 2rem;
  }

  .h2 {
    font-size: 1.5rem;
  }

  .p {
    font-size: 1rem;
  }

  .button {
    width: 180px;
    height: 40px;
    font-size: 16px;
  }
}
