/* Основной контейнер для секции туров */
.tour-section-container {
  width: 100%;
  display: flex;
  flex-direction: column;
  user-select: none;
}

.show-background-picture {
  background-color: rgba(34, 18, 134, 0.3);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  min-height: 220px;
  color: #fff;
  font-size: 45px;
  display: flex;
  align-items: center;
  justify-content: start;
  padding-left: 4%;
  font-weight: bold;
}
.show-background-picture.prCardsBg {
  background-image: url("/src/Assets/img/bgimage-for-tour-section.avif");
}

/* Контейнер для разделения фильтров и карточек туров */
.tour-section-divider-to-two {
  display: flex;
  padding: 30px;
  justify-content: center;
}

/* Стили для секции фильтрации */
.filter-tours-by-their-smth {
  padding: 30px;
  width: 500px;
  height: fit-content;
  border-radius: 10px;
  background-color: #fff;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

.filter-tours-by-their-smth span {
  font-size: 30px;
}
.line-for-filter {
  height: 1px;
  background-color: rgb(92, 138, 255);
  margin: 10px 0 10px 0;
}

.filter-tours-by-their-smth h4 {
  margin-bottom: 15px;
  font-size: 18px;
  font-weight: bold;
  color: #333;
}

.filter-tours-by-their-smth ul {
  list-style-type: none;
  padding: 0;
}

.filter-tours-by-their-smth li {
  margin-bottom: 10px;
  cursor: pointer;
  color: #007bfa;
  font-size: 16px;
  transition: color 0.3s;
}
.filter-tours-by-their-smth li:hover {
  color: #0056b3;
}

/* выпадющее меню */
.country-region-container-main {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.country-region-container {
  width: fit-content;
  height: fit-content;
}
.button {
  border: none;
  padding: 10px 15px;
  font-size: 16px;
  cursor: pointer;
  font-family: Comfortaa;
  text-align: left;
  width: fit-content;
  background-color: #007bff;
  color: white;
  border-radius: 4px;
  transition: background-color 0.3s ease;
  position: relative;
  width: max-content;
}

.button:hover,
.button:focus-visible {
  background-color: #0056b3;
}

.arrow {
  display: inline-block;
  margin-left: 10px;
  width: 0;
  height: 0;
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  border-top: 6px solid white;
  transition: transform 0.3s ease;
}

.arrow.up {
  transform: rotate(180deg);
}

.arrow.down {
  transform: rotate(0deg);
}

.menu {
  max-height: 0;
  overflow: hidden;
  list-style: none;
  border-radius: 0px 0px 4px 4px;
  color: transparent;
  margin: 0;
  padding: 0;
}

.menu-item {
  background-color: #1e8eff;
  width: auto;
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 10px 10px;
  transition: 0.3s ease-in-out;
}

.menu-item:hover {
  background-color: #004bbb;
}

.menu-item.selected {
  background-color: #004bbb;
}

.text-regions {
  font-size: 16px;
  color: #fff;
}

.button.active {
  background-color: #004bbb;
  transform: scale(1.05);
}

.button.active ~ .menu {
  background-color: #004bbb;
  max-height: fit-content;
  color: currentColor;
}
/* выпадющее меню */

/* price */
.category-filter-lol-title {
  font-weight: 900;
}
.category-filter-lol-title.price-title-for-filter {
  margin-bottom: 5px;
}
.price--filter-asap {
  display: flex;
  flex-direction: column;
  padding-top: 20px;
}
.inp-price-for-filter {
  display: flex;
  justify-content: center;
  gap: 10px;
}
.inp-price-for-filter input {
  font-family: Comfortaa;
  padding: 10px;
  width: 80px;
  border-radius: 7px;
  border: 1px solid gray;
  outline: none;
  transition: 0.3s ease;
}
.inp-price-for-filter input:focus {
  border: 1px solid #0056b3;
}

/* price */

/* Стили для карточек туров */
.tours-section-main {
  width: fit-content;
  margin-bottom: 20px;
}

.tours-cards-main {
  display: flex;
  padding: 15px;
  border: 1px solid #ddd;
  border-radius: 8px;
  background-color: #ffffff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s, box-shadow 0.3s;
  margin-bottom: 20px;
  gap: 20px;
}

.tours-cards-main:hover {
  transform: scale(1.01);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

.div-for-cards-preview {
  position: relative;
}
.div-for-cards-preview img {
  width: 300px;
  height: 200px;
  position: relative;
  border-radius: 8px;
  object-fit: cover;
}
.add-to-fav-icon {
  font-size: 35px;
  transition: 0.5s ease;
  color: rgb(161, 161, 161);
  position: absolute;
  top: 10px;
  left: 250px;
}
.add-to-fav-icon:hover {
  transform: scale(1.05);
  color: red;
}
.add-to-fav-icon.notInfav {
  color: black;
}

.main-text-about-tours {
  margin-top: 15px;
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.name-ab-tours {
  font-size: 20px;
  font-weight: bold;
  color: #333;
}

.info-where-tour-starts {
  display: block;
  color: #666;
}

.short-info-main {
  margin-top: 10px;
  color: #444;
}

.price-for-tour {
  font-size: 22px;
  font-weight: bold;
  color: #e74c3c;
}

.all-ab-price-etc {
  display: flex;
  gap: 10px;
  flex-direction: column;
  align-items: center;
  margin-left: auto;
}

.btn-move-to-details {
  padding: 12px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  transition: background-color 0.3s;
  font-family: Comfortaa;
}

.btn-move-to-details:hover {
  background-color: #0056b3;
}
.text-ab-price-tour-card {
  font-size: 22px;
  font-weight: 900;
}
/* Стили для секции фильтрации */
/* Общие стили для контейнера */
/* Общий стиль для всех кнопок */
.country-filter {
  border: none;
  padding: 10px 15px;
  font-size: 16px;
  cursor: pointer;
  font-family: Comfortaa;
  text-align: left;
  width: fit-content;
  background-color: #007bff;
  color: white;
  border-radius: 4px;
  cursor: pointer;
  transition: 0.3s ease;
}

.country-filter:hover {
  background-color: #0056b3;
  color: #fff;
  transform: scale(1.05);
}
.country-filter.selected {
  background-color: #0056b3;
  color: #fff;
  transform: scale(1.05);
}

.country-filter:focus {
  outline: none;
}

.all-button {
  font-weight: bold;
}

/* стиль для дней */

/* pagination */
.pagination-wrapper {
  margin-top: 40px;
}
.pagination-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.pagination-button {
  width: 50px;
  height: 50px;
  font-size: 20px;
  margin-right: 10px;
  font-family: Comfortaa;
  border-radius: 5px;
  border: none;
  color: #fff;
  background-color: #007bff;
  transition: 0.3s ease-in-out;
}
.pagination-button.active {
  background-color: #004fa3;
  transform: scale(1.2);
}
/* pagination */

/* адаптивка */
@media all and (max-width: 1070px) {
  .tours-cards-main {
    flex-direction: column;
  }
  .short-info-main {
    width: 300px;
  }

  .name-ab-tours {
    width: 300px;
  }
  .all-ab-price-etc {
    /* flex-direction: row; */
    justify-content: center;
    margin: 0;
  }
  .btn-move-to-details {
    order: 2;
  }
  .text-ab-price-tour-card {
    order: 1;
    font-size: 20px;
    font-weight: 400;
  }
  .pagination-button {
    width: 40px;
    height: 40px;
    font-size: 20px;
  }
}
@media all and (max-width: 375px) {
  .tours-cards-main {
    width: 275px;
  }
  .div-for-cards-preview img {
    width: 275px;
  }
  .add-to-fav-icon {
    top: 5px;
    left: 233px;
  }
  .short-info-main {
    width: 290px;
  }
  .name-ab-tours {
    width: 260px;
  }
}

/* адаптивка */
