.reviews_modal {
  z-index: 999;
  position: fixed;
  width: 100%;
  height: 100%;
  align-self: flex-start;
  left: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.6);
}
.reviews_modal_content {
  background-color: #e0f7fa;
  width: 35rem;
  height: 25rem;
  display: grid;
  grid-template-rows: 0.5fr 3.05fr 0.45fr;
  border-radius: 0.5rem;
  padding: 2rem;
}
.reviews_rating {
  display: flex;
}
.starsModal {
  cursor: pointer;
}
.modal_span {
  font-size: 1.8rem;
}
.review_modal_inp {
  font-family: Comfortaa;
  width: 95%;
  border: 1px solid #ccc;
  padding: 0.8rem;
  border-radius: 0.4rem;
  outline: none;
  font-size: 1.2rem;
  resize: none;
}
.reviewText,
.review_modal_text_div {
  height: 70%;
}
.review_modal_inp {
  display: flex;
  align-items: flex-start;
}
.reviews_rating {
  height: 15%;
}
.starsModal {
  width: 8%;
  font-size: 200%;
}
.review_modal_btn {
  font-family: Comfortaa;
  border: none;
  border-radius: 0.6rem;
  color: #fff;
  font-size: 1.1rem;
}
.btnAdd {
  background-color: #007bff;
  width: 35%;
  cursor: pointer;
  transition: 0.2s;
}
.btnCancel {
  background-color: #49b6b4;
  width: 20%;
  cursor: pointer;
  transition: 0.2s;
}
.review_modal_btn_div {
  display: flex;
  justify-content: space-between;
}
.btnAdd:hover {
  background-color: #3399ff;
}
.btnCancel:hover {
  background-color: #6cd3d1;
}
@media (max-width: 1100px) {
  .reviews_modal_content {
    width: 30rem;
    height: 20rem;
    border-radius: 0.4rem;
    padding: 1.6rem;
  }
  .starsModal {
    font-size: 150%;
  }
  .modal_span {
    font-size: 1.6rem;
  }
  .reviewText,
  .review_modal_text_div {
    height: 65%;
  }
  .review_modal_btn {
    border-radius: 0.5rem;
    font-size: 0.9rem;
  }
  .review_modal_inp {
    padding: 0.7rem;
    border-radius: 0.3rem;
    font-size: 1rem;
  }
}
@media (max-width: 800px) {
  .reviews_modal_content {
    width: 25rem;
    height: 15rem;
    padding: 1.4rem;
  }
  .starsModal {
    font-size: 120%;
  }
  .modal_span {
    font-size: 1.2rem;
  }
  .reviewText,
  .review_modal_text_div {
    height: 60%;
  }
  .review_modal_btn {
    border-radius: 0.4rem;
    font-size: 0.7rem;
  }
  .review_modal_inp {
    font-size: 0.8rem;
  }
}
@media (max-width: 400px) {
  .review_modal_btn {
    font-size: 0.5rem;
  }
}
