.auth-main {
  display: flex;
  justify-content: center;
  align-items: center;
  height: auto;
  padding: 25px;
  border-radius: 12px;
}

.auth-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 500px;
  height: 580px;
  padding: 25px;
  background-color: #fff;
  transition: 1.25s;
}

.auth-form {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.auth-form__icon {
  object-fit: contain;
  width: 30px;
  margin: 0 5px;
  opacity: 0.5;
  transition: 0.15s;
}

.auth-form__icon:hover {
  opacity: 1;
  transition: 0.15s;
  cursor: pointer;
}

.auth-form__input {
  width: 100%;
  height: 40px;
  margin: 4px 0;
  padding-left: 25px;
  font-size: 13px;
  letter-spacing: 0.15px;
  border: none;
  outline: none;
  font-family: "Montserrat", sans-serif;
  background-color: #ecf0f3;
  transition: 0.25s ease;
  border-radius: 8px;
  box-shadow: inset 2px 2px 4px #d1d9e6, inset -2px -2px 4px #f9f9f9;
  max-width: 350px;
}

.auth-form__input:focus {
  box-shadow: inset 4px 4px 4px #d1d9e6, inset -4px -4px 4px #f9f9f9;
}

.auth-form__span {
  margin-top: 30px;
  margin-bottom: 12px;
  text-align: center;
}

.auth-form__link {
  color: #181818;
  font-size: 15px;
  margin-top: 25px;
  border-bottom: 1px solid #a0a5a8;
  line-height: 2;
}

.auth-title {
  font-size: 34px;
  font-weight: 700;
  line-height: 3;
  color: #181818;
  text-align: center;
  margin: 0;
}

.auth-description {
  font-size: 15px;
  letter-spacing: 0.25px;
  text-align: center;
  line-height: 1.6;
}

.auth-button {
  width: 180px;
  height: 50px;
  border-radius: 25px;
  margin-top: 50px;
  font-weight: 700;
  font-size: 14px;
  letter-spacing: 1.15px;
  background-color: #4b70e2;
  color: #f9f9f9;
  /* box-shadow: 8px 8px 16px #d1d9e6, -8px -8px 16px #f9f9f9; */
  border: none;
  outline: none;
  font-family: Comfortaa;
}
.btn-div-for-moving-to-opposite {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding-top: 40px;
}

/**/
.auth-a-container {
  left: calc(100% - 600px);
}

.auth-b-container {
  left: calc(100% - 600px);
}

.auth-switch {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 850px;
  width: 400px;
  padding: 50px;
  transition: 1.25s;
  background-color: #ecf0f3;
  overflow: hidden;
  box-shadow: 4px 4px 10px #d1d9e6, -4px -4px 10px #f9f9f9;
}

.auth-switch__circle--t {
  top: -300px;
  left: 600px;
  width: 300px;
  height: 300px;
}

.auth-switch__container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 400px;
  height: 530px;
  padding: 50px 55px;
  transition: 1.25s;
  background-color: #fff;
}

.auth-switch__button {
  cursor: pointer;
}

.auth-switch__button:hover {
  box-shadow: 6px 6px 10px #d1d9e6, -6px -6px 10px #f9f9f9;
  transform: scale(0.985);
  transition: 0.25s;
}
.auth-button:hover {
  box-shadow: 6px 6px 10px #d1d9e6, -6px -6px 10px #f9f9f9;
  transform: scale(0.985);
  transition: 0.25s;
}

.auth-button:active,
.auth-button:focus {
  box-shadow: 2px 2px 6px #d1d9e6, -2px -2px 6px #f9f9f9;
  transform: scale(0.97);
  transition: 0.25s;
}
.auth-switch__button:active,
.auth-switch__button:focus {
  box-shadow: 2px 2px 6px #d1d9e6, -2px -2px 6px #f9f9f9;
  transform: scale(0.97);
  transition: 0.25s;
}

/* адаптивка для регистарции*/
@media all and (max-width: 1280px) {
  .auth-main {
    margin: 0px 95px;
  }
  .auth-title {
    font-size: 29px;
    line-height: 40px;
    padding-bottom: 10px;
  }
  .auth-button {
    width: 160px;
    height: 50px;
  }
}
@media all and (max-width: 1024px) {
  .auth-main {
    margin: -20px 60px;
  }
  .auth-switch__container {
    width: 400px;
  }
  .auth-title {
    font-size: 24px;
  }
  .auth-container {
    width: 600px;
  }
  .auth-button {
    width: 140px;
    height: 40px;
  }
  .auth-form__input {
    width: 85%;
    height: 35px;
  }
  .auth-description {
    font-size: 13px;
  }
  .auth-form__span,
  .errors-related-to-reg {
    font-size: 13px;
  }
}
@media all and (max-width: 890px) {
  .auth-main {
    margin: -20px 40px;
  }
  .auth-title {
    font-size: 22px;
  }
  .auth-description {
    font-size: 12px;
  }
  .auth-form__span,
  .errors-related-to-reg {
    font-size: 12px;
  }
}
@media all and (max-width: 768px) {
  .auth-switch__container {
    display: none;
  }

  .auth-container {
    width: 400px;
    background-color: transparent;
  }
  .auth-main {
    margin: 0;
    padding: 0;
  }
  .auth-title {
    font-size: 25px;
  }

  .auth-form__span {
    font-size: 14px;
  }
  .auth-form__input {
    height: 50px;
  }
  .auth-button {
    width: 140px;
    height: 40px;
  }
  .btn-div-for-moving-to-opposite {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding-top: 40px;
  }
  .phone-ask-ab-account {
    display: block;
    font-size: 13px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
  }
  .phone-ask-ab-account a {
    color: #4b70e2;
    text-decoration: none;
  }
  .phone-ask-ab-account a:hover {
    text-decoration: underline;
  }

  .errors-related-to-reg {
    padding-bottom: 20px;
  }
}
@media all and (max-width: 455px) {
  .auth-form {
    height: 120%;
  }
  .phone-logo-for-auth {
    display: block;
    padding: 40px 0 40px;
    width: 250px;
  }
  .auth-title {
    font-size: 26px;
  }

  .auth-container {
    height: 600px;
  }
  .btn-div-for-moving-to-opposite {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding-top: 40px;
  }
  .phone-ask-ab-account {
    display: block;
    font-size: 13px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
  }
  .phone-ask-ab-account a {
    color: #4b70e2;
    text-decoration: none;
  }
  .phone-ask-ab-account a:hover {
    text-decoration: underline;
  }
}
