/* activation code part by amin */
.activation-code-area {
  position: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
  border: 1px solid black;
  padding: 30px;
  background-color: #fff;
  width: 450px;
  border-radius: 2%;
  z-index: 1;
}
.close-icon {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 24px;
  cursor: pointer;
  color: #181818;
}

.letter-for-activation-code {
  font-size: 60px;
  color: #4b70e2;
}
.introduction-to-activation-code-part {
  font-weight: bold;
  font-size: 25px;
}
.main-text-of-activation-code-part {
  font-size: 18px;
  text-align: center;
  width: 320px;
}
.inp-of-activation-code {
  width: 350px;
  height: 50px;
  border-radius: 5px;
  text-align: center;
  border: 1px solid #a3a3a3;
  font-family: Comfortaa;
}
.btn-confirm-and-send {
  width: 300px;
  height: 50px;
  font-size: 20px;
  font-family: Comfortaa;
  background-color: #4b70e2;
  border: none;
  border-radius: 5px;
  color: #fff;
}
.btn-confirm-and-send:hover {
  box-shadow: 6px 6px 10px #d1d9e6, -6px -6px 10px #f9f9f9;
  transform: scale(0.985);
  transition: 0.25s;
}

.btn-confirm-and-send:active,
.btn-confirm-and-send:focus {
  box-shadow: 2px 2px 6px #d1d9e6, -2px -2px 6px #f9f9f9;
  transform: scale(0.97);
  transition: 0.25s;
}
.activation-code-error {
  font-size: 17px;
}
.send-user-to-reg-again {
  text-decoration: none;
  color: #4b70e2;
}
.send-user-to-reg-again:hover {
  text-decoration: underline;
}
