.main-page-container {
  display: flex;
  overflow: hidden;
  background-color: transparent;
  padding: 40px 100px;
  gap: 10px;
}

/*  */
.text-container {
  position: relative;
  background-image: url("/src/Assets/img/main-picture-main-page.jpg");
  background-position: center;
  background-size: cover;
  padding: 10px 24px 30px 24px;
  width: 100%;
  background-color: #e0f7fa;
  border-radius: 20px;
  box-shadow: 0 8px 12px rgba(0, 0, 0, 0.2);
  color: white;
}

.text-container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 20px;
  z-index: 1;
}

.text-container > * {
  position: relative;
  z-index: 2;
}

/*  */

.title {
  font-size: 50px;
  font-weight: bold;
  color: #fff;
}

.description {
  font-size: 22px;
  margin-top: 16px;
  color: #fff;
  font-weight: 100;
}

.consultation-button {
  margin-top: 24px;
  color: #fff;
  background-color: #007bff;
  padding: 8px 16px;
  border-radius: 6px;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s;
  font-family: Comfortaa, sans-serif;
  width: 260px;
  height: 50px;
  font-size: 20.8px;
}

.consultation-button:hover {
  background-color: #1565c0;
}

/*  */
.image-container {
  position: relative;
  padding: 10px 24px 30px 24px;
  width: 100%;
  box-shadow: 0 8px 12px rgba(0, 0, 0, 0.2);
  border-radius: 20px;
  overflow: hidden;
  background-image: url("/src/Assets/img/pic_for_mainBanner.jpeg");
  background-size: cover;
  background-repeat: no-repeat;
}

.image-container::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 20px;
  z-index: 1;
}

.image-container > * {
  position: relative;
  z-index: 2;
}

/*  */

@media all and (min-width: 1753px) {
  .title {
    font-size: 4rem;
  }
  .text-container {
    height: 600px;
  }
  .description {
    font-size: 1.9rem;
  }
  .consultation-button {
    margin-top: 1.5rem;
    color: #fff;
    background-color: #0d47a1;
    padding: 0.5rem 1rem;
    border-radius: 0.375rem;
    border: none;
    cursor: pointer;
    transition: background-color 0.3s;
    font-family: Comfortaa, sans-serif;
    width: 300px;
    height: 60px;
    font-size: 1.5rem;
  }
}
@media all and (max-width: 1220px) {
  .title {
    font-size: 45px;
  }
  .description {
    font-size: 20px;
  }
  .consultation-button {
    margin-top: 20px;
    padding: 8px 16px;
    width: 240px;
    height: 50px;
    font-size: 19px;
  }
}
@media all and (max-width: 1120px) {
  .title {
    font-size: 40px;
  }
  .main-page-container {
    padding: 40px 80px;
  }
  .description {
    font-size: 18px;
  }
}
@media all and (max-width: 1120px) {
  .title {
    font-size: 39px;
  }
  .main-page-container {
    padding: 25px 70px;
  }
  .consultation-button {
    width: 220px;
    font-size: 17px;
  }
}
@media all and (max-width: 946px) {
  .title {
    font-size: 35px;
  }
  .main-page-container {
    padding: 10px 30px;
  }
  .description {
    font-size: 16px;
  }
  .consultation-button {
    width: 190px;
    font-size: 15px;
    height: 40px;
    padding: 0;
  }
}
@media all and (max-width: 740px) {
  .text-container {
    height: 90%;
  }

  .title {
    font-size: 30px;
  }
  .description {
    font-size: 15px;
  }
  .consultation-button {
    width: 150px;
    font-size: 13px;
    height: 35px;
  }
}
@media all and (max-width: 600px) {
  .text-container::before {
    background-color: rgba(0, 0, 0, 0.4);
  }
  .main-page-container {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }

  .image-container {
    display: none;
  }
  .main-page-container {
    gap: 20px;
  }
  .text-container {
    height: auto;
    gap: 10px;
  }
  .text-container {
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  .title {
    font-size: 32px;
    text-align: center;
  }
  .description {
    text-align: center;
    font-size: 17px;
  }
  .consultation-button {
    width: 190px;
    font-size: 16px;
    height: 45px;
  }
  .main-page-container {
    padding: 10px 65px;
  }
}
@media all and (max-width: 490px) {
  .main-page-container {
    padding: 10px 40px;
  }
  .title {
    font-size: 35px;
  }
}
@media all and (max-width: 401px) {
  .main-page-container {
    padding: 10px 33px;
  }
}
/* Slider fucked up */
.carousel {
  padding: 2rem 4rem;
  background-color: transparent;
  color: hsl(0, 0%, 100%);
  height: auto;
}
.box {
  height: 300px;
}

.photo-preview-of-slider {
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
  border-radius: 5%;
  object-fit: cover;
}

.price-about-photo-preview {
  color: #000;
  font-size: 25px;
  margin: 15px 0;
  font-weight: normal;
}
.name-about-photo-preview {
  color: #000;
  font-size: 22px;
  margin: 0 0 10px 0;
  font-weight: normal;
}
.description-text-about-photo-preview {
  color: #000;
  font-size: 16px;
  margin: 0;
  font-weight: normal;
}
.slick-dots {
  position: unset !important;
}
.slick-slide div {
  margin: 0 3px;
}
.slick-prev {
  left: -40px !important;
}
.introduction-to-slides {
  visibility: visible !important;
  display: flex;
  justify-content: center;
  padding-bottom: 10px;
  font-size: 3.5vh;
  text-transform: uppercase;
  color: #000;
  font-weight: bold;
  padding-bottom: 40px;
}
.slick-dots li.slick-active button:before {
  color: #483d8b !important;
}
.slick-dots li button:before {
  color: #6a5bcc !important;
  font-size: 1rem !important;
  top: 2rem !important;
}
.slick-prev:before,
.slick-next:before {
  font-size: 40px !important;
  color: #007bff !important;
}

/* анимированный arrow shit */
.scroll-indicator {
  visibility: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.canvas-for-moving-finger {
  position: absolute;
  right: 10px;
  top: 60%;
  transform: translateY(-50%);
  background-color: #333333;
  padding: 10px 0;
  border-radius: 3px;
}

.hand {
  width: 40px;
  height: 40px;
  background-image: url("/src/Assets/img/hand-icon-for-scroll.svg");
  animation: bounce 2s infinite;
  background-size: contain;
}

@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateX(0);
  }
  40% {
    transform: translateX(20px);
  }
  60% {
    transform: translateX(10px);
  }
}

/* анимированный arrow shit */

@media all and (max-width: 1090px) {
  .carousel {
    padding: 2rem 3rem;
  }
}
