.review_card_rating {
  display: flex;
}

.btn-for-owner-of-review {
  align-items: center;
  display: flex;
  gap: 5px;
}

.btn-for-owner-of-review button {
  font-family: Comfortaa;
  font-size: 10px;
  padding: 7px;
  background-color: #007bff;
  color: #fff;
  width: -moz-fit-content;
  width: fit-content;
  border: none;
  border-radius: 7px;
  transition: 0.5s;
}
.btn-for-owner-of-review button:hover {
  background-color: #0056b3;
}
.review_card {
  background-color: #e0f7fa;
  margin: 0 0.9rem 0.9rem 0;
  display: flex;
  flex-direction: column;
  padding: 1.7rem;
  height: auto;
  box-shadow: 0px 0px 4px gray;
  border-radius: 0.2rem;
  cursor: default;
  width: 100%;
  box-sizing: border-box;
}

.reviews_cards {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 1rem;
}

.reviews_text {
  white-space: normal;
  overflow-wrap: break-word;
  word-wrap: break-word;
  overflow: hidden;
  text-overflow: ellipsis;
}

.reviews_title {
  font-size: 1.9rem;
  padding-bottom: 1.6rem;
  cursor: default;
}

.reviews_container {
  padding: 3rem 4rem 1rem 4rem;
}

.review_card_name {
  display: flex;
  align-items: center;
  gap: 10px;
  font-weight: 500;
  font-size: 1.1rem;
}

.revPage {
  position: relative;
  cursor: default;
  background-image: url(../../Assets/img/headerBgForReviewsPage.jpg);
}
.revPage::before {
  position: absolute;
  content: "";
  background-color: rgba(34, 18, 134, 0.3);
  width: 100%;
  height: 100%;
  z-index: 0;
  left: 0;
}
.show-background-title {
  z-index: 1;
  font-size: 3vw;
}
.reviews_upper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.reviews_modal_btn {
  background-color: #007bff;
  color: #fff;
  border-radius: 0.5rem;
  border: none;
  cursor: pointer;
  font-family: Comfortaa;
  height: 100%;
  width: 100%;
}

.reviews_modal_div {
  padding-bottom: 1.5rem;
  padding-right: 1rem;
  height: 3rem;
  width: 10rem;
}

.reviews_modal_btn:hover {
  background-color: #1565c0;
}

.review_card_show_more_div {
  display: flex;
  justify-content: flex-end;
  padding-top: 0.3rem;
}

.review_card_show_more_btn {
  color: gray;
  border: none;
  background: none;
  font-family: Comfortaa;
  cursor: pointer;
  transition: 0.1s;
}

.review_card_show_more_btn:hover {
  color: rgb(1, 0, 77);
}

/* Media Queries */
@media (max-width: 1100px) {
  .review_card {
    font-size: 0.7rem;
    padding: 1rem;
  }
  .review_card_name {
    font-size: 0.8rem;
  }
  .reviews_modal_div {
    width: 8rem;
    height: 2.5rem;
  }
  .reviews_modal_btn {
    font-size: 0.7rem;
  }
  .review_card_show_more_btn {
    font-size: 0.6rem;
  }
}

@media (max-width: 800px) {
  .reviews_cards {
    display: flex;
    flex-wrap: wrap;
  }
  .review_card {
    margin-right: 0;
  }
  .reviews_modal_div {
    padding: 0;
  }
  .reviews_title {
    font-size: 1.5rem;
  }
  .show-background-title {
    font-size: 3rem;
  }
}

@media (max-width: 750px) {
  .reviews_container {
    padding: 2.5rem 2rem 1rem 2rem;
  }
  .reviews_modal_btn {
    font-size: 0.6rem;
  }
}

@media (max-width: 600px) {
  .reviews_modal_div {
    width: 5rem;
  }
  .show-background-title {
    font-size: 2.5rem;
  }
}

@media (max-width: 400px) {
  .reviews_container {
    padding: 2rem 1rem 1rem 1rem;
  }
  .review_card {
    font-size: 0.7rem;
    padding: 1.1rem;
  }
  .reviews_title {
    font-size: 1.3rem;
  }
  .review_card_name {
    font-size: 0.9rem;
  }
}
@media (max-width: 350px) {
  .reviews_upper {
    flex-wrap: wrap;
    padding-bottom: 0.8rem;
  }
  .reviews_title {
    padding-bottom: 1rem;
    font-size: 6vw;
  }
  .show-background-title {
    font-size: 12vw;
  }
  .reviews_modal_div {
    width: 8rem;
    height: 1.5rem;
  }
}
