.scroll-to-top {
	position: fixed;
	bottom: 20px;
	right: 20px;
	padding: 15px 15px;
	background-color: #0d47a1;
	color: white;
	border: none;
	border-radius: 50%;
	cursor: pointer;
	display: none;
	z-index: 1000;
	font-size: 40px;
}

.scroll-to-top:hover {
	background-color: #08306b;
}

@media all and (max-width: 1024px) {
	.scroll-to-top {
		padding: 10px 10px;
	}
}
