/* .about_container {
  position: relative;
} */

.about_company_info {
  position: relative;
  overflow: hidden;
  margin-top: 1rem;
  display: flex;
}

#background-video {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  object-fit: cover;
  transform: translate(-50%, -50%);
  z-index: -1;
  border-radius: 1.4rem;
  width: 98vw;
}

.about_story_of_company {
  background-color: #e0f7fa;
  padding: 2rem;
  box-shadow: 0 8px 12px rgba(0, 0, 0, 0.2);
  border-radius: 1.4rem;
  margin: 1rem;
  display: flex;
  align-items: center;
  font-size: 0.99rem;
}

.about_story_of_company_upper_text_idea_span {
  color: #0d47a1;
}

.about_story_of_company_upper_text {
  font-size: 2rem;
}

.about_story_of_company_lower_text {
  padding-top: 2.1rem;
}

.about_story_of_company_img {
  background-color: #f3f5f9;
  background-position: 0% 99.974%;
  background-size: 100%;
  background-repeat: no-repeat;
  width: 260vw;
  height: 28vw;
  border-radius: 1.5rem;
  transform-origin: center;
}

.about_story_of_company_text {
  padding: 0 2rem 2rem 1rem;
  word-spacing: 0.12rem;
}

.about_story_of_company_lower_text_dear_friends_span,
.about_director {
  font-weight: bold;
}

.about_social_media_icons {
  height: 2rem;
  width: auto;
}

.about_company_info_left {
  display: flex;
  align-items: center;
  padding: 4rem;
}

.about_company_info_left_div {
  font-weight: 500;
  font-size: 3.5rem;
  display: flex;
  flex-wrap: wrap;
}

.about_company_info_left_div:first-child {
  color: #e0f7fa;
}

.about_company_info_right {
  display: flex;
  align-items: center;
  padding: 5rem;
  padding-left: 7rem;
  color: #ffffff;
  letter-spacing: 0.01rem;
  word-spacing: 0.1rem;
}

.about_company_info_left_span {
  white-space: nowrap;
}

.about_company_info_name {
  text-align: center;
  font-size: 60px;
  font-weight: 900;
  color: #115ccb;
}
@media (max-width: 1200px) {
  .about_story_of_company_img {
    height: 30vw;
    width: 370vw;
  }
}
/* Media Queries */
@media (max-width: 1025px) {
  .about_story_of_company {
    padding-top: 2.4rem;
    padding-bottom: 2rem;
  }

  .about_story_of_company_text {
    font-size: 0.873rem;
    padding-right: 3rem;
  }

  .about_story_of_company_upper_text {
    font-size: 1.45rem;
  }

  .about_story_of_company_img {
    width: 350vw;
    height: 30vw;
    margin-top: 1.5rem;
  }

  .about_company_info_left_div {
    font-size: 2.9rem;
  }

  .about_company_info_left {
    align-items: flex-start;
  }

  .about_company_info_right {
    padding: 3rem;
    font-size: 0.9rem;
  }
}
/* @media (min-width: 770px) {
    .about_story_of_company_img {
      height: 23vw;
    }
  } */
@media (max-width: 959px) {
  .about_story_of_company_upper_text {
    font-size: 2rem;
  }

  .about_story_of_company {
    flex-wrap: wrap;
    padding-top: 3.5rem;
  }

  .about_story_of_company_img {
    background-position: 0% 52.49%;
    background-size: 100%;
    margin-top: 4rem;
    height: 59vw;
  }

  .about_story_of_company_text {
    font-size: 0.9rem;
  }

  .about_company_info {
    flex-wrap: wrap;
  }

  .about_company_info_left {
    flex-wrap: wrap;
    width: 100%;
    padding-bottom: 0;
  }

  .about_company_info_left_div {
    flex-direction: column;
  }

  .about_company_info_right {
    padding-top: 2rem;
  }
}

@media (max-width: 426px) {
  .about_story_of_company_text {
    padding: 0;
  }

  .about_story_of_company {
    padding-top: 2.5rem;
  }

  .about_story_of_company_img {
    height: 45vw;
    width: 80vw;
  }
}

@media (max-width: 376px) {
  .about_story_of_company_img {
    height: 26vh;
  }
  .about_company_info_left {
    padding-left: 2rem;
  }
}

@media (max-width: 321px) {
  .about_story_of_company_img {
    height: 47vw;
  }
  .about_company_info_left {
    padding-left: 1.8rem;
  }
  .about_company_info_left_div {
    font-size: 2.5rem;
  }
}
@media (min-width: 2560px) {
  .about_story_of_company_lower_text {
    font-size: 1.5rem;
  }
  .about_company_info_right {
    font-size: 1.2rem;
  }
  .about_story_of_company_upper_text {
    font-size: 3.5rem;
  }
  .about_company_info_left_div {
    font-size: 4.5rem;
  }
}
