.watch-ab-tour-more-section {
  padding: 0 100px 30px 100px;
}

.title-of-tour {
  margin: 30px 0px;
}

.title-for-tour-for-watch-more {
  font-size: 34px;
  margin: 0;
  font-weight: 700;
}

.add-to-fav-icon-in-more-tour {
  position: absolute;
  top: 13px;
  left: 835px;
  font-size: 40px;
  transition: 0.5s ease;
  color: rgb(161, 161, 161);
}
.add-to-fav-icon-in-more-tour:hover {
  transform: scale(1.05);
}
/* slider area */
.place-where-we-put-our-pic-ab-tour {
  position: relative;
}

.slider-and-cards-with-price-and-order {
  display: flex;
  justify-content: center;
  gap: 50px;
}

.slider-with-picture-preview-main {
  width: 900px;
}

.photo-review-for-tour-more-inf {
  width: 894px !important;
  height: 550px !important;
  border-radius: 10px !important;
  margin-left: 5px;
}

.btn-for-price-for-tour-itself {
  border: none;
  border-radius: 20px;
}

.summery-info-ab-tour-section {
  display: flex;
  flex-direction: column;
}
.summery-info-ab-tour-section h3 {
  margin: 0;
  padding: 0;
  font-size: 18px;
  font-weight: 500;
}
.summery-info-ab-tour-section p {
  margin: 5px 0;
  padding: 10px 0;
  font-size: 16px;
  line-height: 1.3;
  font-weight: 200;
}

.cards-with-info-ab-price-and-order {
  margin-top: 100px;
  display: flex;
  flex-direction: column;
  border: 1px gray solid;
  padding: 25px;
  height: fit-content;
  width: 280px;
  background-color: #fff;
  border-radius: 10px;
}

.price-for-tour-itself {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.price-for-tour-itself h4 {
  font-size: 18px;
  font-weight: 500;
  margin: 0;
}
.price-for-tour-itself h5 {
  font-size: 25px;
  margin: 0;
  color: red;
  font-weight: 900;
}
.price-for-tour-itself h5 span {
  font-size: 15px;
  color: #515151;
}
.all-btn-related-to-order-watch-tour-more {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.btn-for-price-for-tour-itself.choose-car-btn-special {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
}

.choose-car-btn-special-icon {
  font-size: 23px;
  border-radius: 50%;
}

.btn-for-price-for-tour-itself {
  padding: 15px;
  font-family: Comfortaa;
  background-color: #007bff;
  transition: 0.5s ease;
  color: #fff;
  font-size: 20px;
  border-radius: 10px;
  width: 250px;
  cursor: pointer;
}
.btn-for-price-for-tour-itself:hover {
  background-color: #0056b3;
}

/* slider area */

/* all important info */
.section-where-all-info-ab-tour-yapi {
  border: 1px solid #ddd;
  margin-top: 60px;
  border-radius: 10px;
  background-color: #fff;
}
/* highlights */
.section-about-overview-more-info-and-etc {
  border-radius: 10px;
}

.highlights-ab-tour-in-more-info {
  display: flex;
  flex-direction: column;
  gap: 7px;
  padding: 50px 65px;
  margin: 0;
}

.header-of-section-ab-overview-and-etc {
  padding-top: 40px;
}

.header-of-section-ab-overview-and-etc h3 {
  font-size: 35px;
  color: #007bff;
  margin: 0;
  padding: 0px 35px;
}

.callout-title-icon {
  display: inline-block;
  vertical-align: text-bottom;
  width: 10px;
  height: 30px;
  background-color: #ff5b1b;
}

.bt_tours_row {
  border-bottom: 1px dashed #a4a4a4;
  font-size: 14px;
  clear: both;
  padding: 20px 0;
}

.bt_tours_col {
  display: flex;
  align-items: center;
  gap: 30px;
  width: 47%;
  padding-right: 15px;
  float: left;
}

.overview_field_title {
  color: #4a4a4a;
  font-weight: bold;
  padding-right: 0;
  width: 160px;
  float: left;
  display: flex;
  align-items: center;
}
.thin-line-divider-section-into-two {
  height: 1px;
  color: #000;
  background-color: #000;
  border: none;
  text-decoration: dotted;
  margin: 0;
}
.start-loc-end-loc-dur-count-side {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 10px 40px;
}

.icon-for-overview-in-loc-coun-dur {
  font-size: 25px;
  padding-right: 10px;
}
/* highlights */

/* is it for you section */

.info-ab-tour-that-we-need {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  padding: 30px 60px 0 60px;
  gap: 30px;
}
.info-ab-tour-that-we-need-itself {
  display: flex;
  flex-direction: column;
  width: 90%;
}

.info-ab-tour-that-we-need-itself h4 {
  padding-top: 20px;
  color: #007bff;
  font-size: 20px;
  margin: 0;
  font-weight: 600;
}
.info-ab-tour-that-we-need-itself span {
  color: #ff5b1b;
}
.info-ab-tour-that-we-need-itself p {
  font-size: 16px;
  font-weight: 200;
}

/* is it for you section */

/* suggestion info */
.special-notes-tour-more-info-section-suggestions {
  padding: 30px 60px 30px 60px;
}
.special-notes-tour-more-info-section-suggestions ul {
  margin: 0;
  padding: 0;
}
.special-notes-tour-more-info-section-suggestions ul li p {
  font-size: 16px;
  font-weight: 200;
  margin: 0;
  line-height: 1.4;
}
.special-notes-tour-more-info-itself h3 {
  margin: 0;
  padding: 20px 0;
  font-size: 20px;
  text-decoration: underline;
  font-weight: 500;
}
/* suggestion info */

/* recommendation-area */
.recommendations-for-users-tours-section-main {
  padding: 30px 0;
}
.recommendations-for-users-tours-text {
  display: flex;
  flex-direction: column;
  padding-bottom: 20px;
  line-height: 1.4;
}
.recommendations-for-users-tours-text h3 {
  margin: 0;
  font-size: 35px;
}
.recommendations-for-users-tours-text span {
  font-size: 20px;
}
.recommendations-for-users-tours {
  display: flex;
  justify-content: center;
  gap: 20px;
}

/* cards fav list */
.fav-card-section-recom {
  padding: 20px;
  border-radius: 10px;
  height: auto;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.main-info-ab-tour-in-fav-recom {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.main-info-ab-tour-in-fav-recom span {
  width: 200px;
}

.fav-list-image-preview-recom {
  width: 220px;
  height: 190px;
  border-radius: 10px;
  position: relative;
}

.heart-to-remove-from-fav-recom {
  position: absolute;
  right: 30px;
  top: 30px;
  font-size: 35px;
  color: red;
  transition: 0.5s ease;
}

.heart-to-remove-from-fav-recom:hover {
  transform: scale(1.1);
}

.fav-card-title-recom {
  font-size: 17px;
  font-weight: 900;
}

.fav-card-description-recom {
  font-size: 16px;
  font-weight: 400;
}

.fav-card-direction-recom {
  font-size: 16px;
  font-weight: 300;
}

.btn-move-to-details-fav-recom {
  padding: 12px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  width: 100%;
  transition: background-color 0.5s;
  font-family: Comfortaa;
}

.btn-move-to-details-fav-recom:hover {
  background-color: #0056b3;
}

.all-ab-price-etc-fav-recom {
  display: flex;
  text-align: end;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  gap: 5px;
}

.other-info-ab-tours-in-fav-recom {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.text-ab-price-tour-card-recom.fav-cards-price-ab-recom {
  font-size: 20px;
  margin-top: 20px;
}

