.fav-list-section {
  width: 100%;
  display: flex;
  flex-direction: column;
  user-select: none;
}

.show-background-picture.favBgPic {
  background-image: url("/src/Assets/img/bg-image-for-favList-section.avif") !important;
}

.count-of-tour-in-fav {
  background-color: red;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-left: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 40px;
}

.fav-text {
  color: #555;
}
/* if-there-is-no-fav-tour-in */
.if-there-is-no-fav-tour-in {
  border-radius: 10px;
  background-color: #f8f8f8;
  border: none;
  border-radius: 10px;
  width: 872px;
  height: auto;
}
.if-there-is-no-fav-tour-in-icon {
  font-size: 50px;
  color: #007bff;
}
.if-there-is-no-fav-tour-in-itself {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  line-height: 1.7;
  padding: 30px;
  text-align: center;
}
/* if-there-is-no-fav-tour-in */

/* cards */
.fav-cards-itself-section {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  padding: 26px;
  box-sizing: border-box;
  border: 1px solid black;
  border-radius: 20px;
  background-color: #f8f8f8;
  border: none;
  height: auto;
}

.fav-card-section {
  padding: 20px;
  border-radius: 10px;
  height: 410px;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.main-info-ab-tour-in-fav {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.main-info-ab-tour-in-fav span {
  width: 200px;
}

.fav-list-image-preview {
  width: 220px;
  height: 190px;
  border-radius: 10px;
  position: relative;
}

.heart-to-remove-from-fav {
  position: absolute;
  right: 30px;
  top: 30px;
  font-size: 35px;
  color: red;
  transition: 0.5s ease;
}
.heart-to-remove-from-fav:hover {
  transform: scale(1.1);
}

.fav-card-title {
  font-size: 17px;
  font-weight: 900;
}

.fav-card-description {
  font-size: 16px;
  font-weight: 400;
}

.fav-card-direction {
  font-size: 16px;
  font-weight: 300;
}

.btn-move-to-details-fav {
  padding: 12px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  width: 100%;
  transition: background-color 0.5s;
  font-family: Comfortaa;
}
.btn-move-to-details-fav:hover {
  background-color: #0056b3;
}
.all-ab-price-etc-fav {
  display: flex;
  text-align: end;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  gap: 5px;
}

.other-info-ab-tours-in-fav {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.text-ab-price-tour-card.fav-cards-price-ab {
  font-size: 20px;
}
/* cards */

/* filter */
.filter-for-tours-lol {
  border-radius: 10px;
  background-color: #f8f8f8;
  padding: 15px 45px 15px 30px;
  height: fit-content;
  width: 220px;
  margin-right: 25px;
}
.filter-for-tours-lol h3 {
  font-size: 25px;
  margin-bottom: 0;
}
.category-filter-lol {
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.category-filter-lol-title {
  margin: 0;
  padding: 0;
  padding-bottom: 10px;
  font-size: 20px;
}

.category-filter-lol-title.day-long-count {
  padding: 20px 0;
}

/* filter */

/* адаптивка */
@media all and (max-width: 1440px) {
  .fav-cards-itself-section {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media all and (max-width: 1070px) {
  .btn-move-to-details-fav {
    order: 2;
  }
}

@media all and (max-width: 1200px) {
  .fav-cards-itself-section {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media all and (max-width: 920px) {
  .fav-card-section {
    flex-direction: row;
    gap: 10px;
    height: 330px;
  }
  .fav-cards-itself-section {
    grid-template-columns: repeat(1, 1fr);
  }
  .btn-move-to-details-fav {
    height: fit-content;
  }
  .heart-to-remove-from-fav {
    right: 143px;
  }
}
@media all and (max-width: 768px) {
  .filter-for-tours-lol {
    margin: 0;
    text-align: center;
  }
  .if-there-is-no-fav-tour-in {
    width: auto;
  }
  .tour-section-divider-to-two {
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }
  .main-info-ab-tour-in-fav {
    flex-direction: row;
    gap: 15px;
  }
  .fav-card-section {
    height: auto;
  }
  .other-info-ab-tours-in-fav {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  .heart-to-remove-from-fav {
    right: 360px;
  }
  .category-filter-lol {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
@media all and (max-width: 650px) {
  .main-info-ab-tour-in-fav {
    flex-direction: column;
    gap: 15px;
  }
  .fav-card-section {
    flex-direction: column;
  }
  .fav-card-section {
    height: auto;
  }
  .filter-for-tours-lol {
    margin: 0;
  }
  .heart-to-remove-from-fav {
    right: 28px;
  }
  .flex-1 {
    padding: 10px 15px;
    width: 95%;
    border-radius: 8px;
  }
  .dropdown {
    display: flex;
    justify-content: end;
  }
  .flex {
    flex-direction: column;
    gap: 10px;
  }
  .dropdown-toggle {
    order: 2;
  }
  .dropdown {
    display: contents;
  }
  .bg-primary {
    border-radius: 8px;
  }
  .dropdown-toggle {
    display: none;
  }
  .dropdown-menu {
    position: unset;
    order: 3;
    z-index: 1;
  }
}
@media all and (max-width: 340px) {
  .fav-cards-itself-section {
    padding: 10px;
  }
}
@media all and (max-width: 320px) {
  .show-background-picture {
    font-size: 40px;
  }
}
/* адаптивка */
