.log-auth-main {
  display: flex;
  justify-content: center;
  align-items: center;
  height: auto;
  padding: 25px;
  border-radius: 12px;
}

.log-auth-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 500px;
  height: 580px;
  padding: 25px;
  background-color: #fff;
  transition: 1.25s;
}

.log-auth-form {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 100%;
}

.log-auth-form__icon {
  object-fit: contain;
  width: 30px;
  margin: 0 5px;
  opacity: 0.5;
  transition: 0.15s;
}

.log-auth-form__icon:hover {
  opacity: 1;
  transition: 0.15s;
  cursor: pointer;
}

.log-auth-form__input {
  width: 100%;
  height: 40px;
  margin: 4px 0;
  padding-left: 25px;
  font-size: 13px;
  letter-spacing: 0.15px;
  border: none;
  outline: none;
  font-family: "Montserrat", sans-serif;
  background-color: #ecf0f3;
  transition: 0.25s ease;
  border-radius: 8px;
  box-shadow: inset 2px 2px 4px #d1d9e6, inset -2px -2px 4px #f9f9f9;
  max-width: 350px;
}

.log-auth-form__input:focus {
  box-shadow: inset 4px 4px 4px #d1d9e6, inset -4px -4px 4px #f9f9f9;
}

.log-auth-form__span {
  margin-top: 30px;
  margin-bottom: 12px;
}
.error-text {
  margin: 20px 0px 0px 0px;
}

.log-btn-div-for-moving-to-opposite {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.log-auth-form__link:hover {
  text-decoration: underline;
}

.log-auth-title {
  font-size: 34px;
  font-weight: 700;
  line-height: 3;
  color: #181818;
}

.log-auth-description {
  font-size: 14px;
  letter-spacing: 0.25px;
  text-align: center;
  line-height: 1.6;
}

.log-auth-button {
  width: 180px;
  height: 50px;
  border-radius: 25px;
  margin-top: 40px;
  font-weight: 700;
  font-size: 14px;
  letter-spacing: 1.15px;
  background-color: #4b70e2;
  color: #f9f9f9;
  border: none;
  outline: none;
  font-family: Comfortaa;
}

.log-auth-a-container {
  left: calc(100% - 600px);
}

.log-auth-b-container {
  left: calc(100% - 600px);
}

.log-auth-switch {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 850px;
  width: 400px;
  padding: 50px;
  transition: 1.25s;
  background-color: #ecf0f3;
  overflow: hidden;
  box-shadow: 4px 4px 10px #d1d9e6, -4px -4px 10px #f9f9f9;
}

.log-auth-switch__circle--t {
  top: -300px;
  left: 600px;
  width: 300px;
  height: 300px;
}

.log-auth-switch__container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 400px;
  height: 530px;
  padding: 50px 55px;
  transition: 1.25s;
  background-color: #fff;
}

.log-auth-switch__button {
  cursor: pointer;
}

.log-auth-switch__button:hover {
  box-shadow: 6px 6px 10px #d1d9e6, -6px -6px 10px #f9f9f9;
  transform: scale(0.985);
  transition: 0.25s;
}
.log-auth-button:hover {
  box-shadow: 6px 6px 10px #d1d9e6, -6px -6px 10px #f9f9f9;
  transform: scale(0.985);
  transition: 0.25s;
}

.log-auth-button:active,
.log-auth-button:focus {
  box-shadow: 2px 2px 6px #d1d9e6, -2px -2px 6px #f9f9f9;
  transform: scale(0.97);
  transition: 0.25s;
}
.log-auth-switch__button:active,
.log-auth-switch__button:focus {
  box-shadow: 2px 2px 6px #d1d9e6, -2px -2px 6px #f9f9f9;
  transform: scale(0.97);
  transition: 0.25s;
}

.log-phone-ask-ab-account {
  display: none;
}

/* адаптивка для регистарции */
@media all and (max-width: 1280px) {
  .log-auth-main {
    margin: 0px 95px;
  }
  .log-auth-title {
    font-size: 29px;
  }
  .log-auth-button {
    width: 160px;
    height: 50px;
  }
}
@media all and (max-width: 1024px) {
  .log-auth-main {
    margin: -20px 60px;
  }
  .log-auth-switch__container {
    width: 400px;
  }
  .log-auth-title {
    font-size: 24px;
  }
  .log-auth-container {
    width: 600px;
  }
  .log-auth-button {
    width: 140px;
    height: 40px;
  }
  .log-auth-form__input {
    width: 85%;
    height: 35px;
  }
  .log-auth-description {
    font-size: 13px;
  }
  .log-auth-form__span,
  .error-text {
    font-size: 13px;
  }
}
@media all and (max-width: 890px) {
  .log-auth-main {
    margin: -20px 40px;
  }
  .log-auth-title {
    font-size: 22px;
  }
  .log-auth-description {
    font-size: 12px;
  }
  .log-auth-form__span {
    font-size: 12px;
  }
}
@media all and (max-width: 755px) {
  .log-auth-switch__container {
    display: none;
  }

  .log-auth-container {
    width: 400px;
    background-color: transparent;
  }
  .log-auth-main {
    margin: 0;
    padding: 0;
  }
  .log-auth-title {
    font-size: 30px;
  }

  .log-auth-form__span {
    font-size: 15px;
  }
  .log-auth-form__input {
    height: 50px;
  }
  .log-auth-button {
    width: 140px;
    height: 40px;
    order: 2;
    margin: 0;
  }
  .error-text {
    font-size: 14px;
  }
  /*  */
  .log-btn-div-for-moving-to-opposite {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding-top: 0px;
    gap: 20px;
  }

  .log-phone-ask-ab-account {
    order: 2;
    display: block;
    font-size: 14px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
    margin: 20px 0px;
  }
  .log-phone-ask-ab-account a {
    color: #4b70e2;
    text-decoration: none;
  }
  .log-phone-ask-ab-account a:hover {
    text-decoration: underline;
  }
  /*  */
}
@media all and (max-width: 455px) {
  .log-auth-button {
    margin: 0;
  }
  .log-auth-form {
    height: 120%;
  }
  .log-auth-title {
    font-size: 29px;
    line-height: 2;
    text-align: center;
  }

  .log-auth-container {
    height: 600px;
  }
  .log-btn-div-for-moving-to-opposite {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding-top: 0px;
  }

  .log-phone-ask-ab-account {
    display: block;
    font-size: 13px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
  }
  .error-text {
    font-size: 13px;
  }
  .log-phone-ask-ab-account a {
    color: #4b70e2;
    text-decoration: none;
  }
  .log-phone-ask-ab-account a:hover {
    text-decoration: underline;
  }
}
@media all and (max-width: 330px) {
  .log-auth-title {
    text-align: center;
    font-size: 26px;
    line-height: 2;
  }
  .log-auth-container {
    padding: 0px;
  }
  .log-auth-form__input {
    width: 75%;
    height: 50px;
  }
  .log-auth-form__span {
    font-size: 80%;
  }
  .log-auth-button {
    height: 50px;
  }
}
