.reviews-main-container {
  display: flex;
  gap: 2.5rem;
  flex-direction: column;
  align-items: center;
  margin: 2rem 2rem 2.5rem 2rem;
}
.reviews-header {
  align-self: flex-start;
  color: black;
  font-size: 2.5rem;
  font-weight: 600;
}

.reviews-list {
  display: flex;
  gap: 1rem;
}

.review-card {
  background-color: #e0f7fa;
  border-radius: 1rem;
  display: flex;
  flex-direction: column;
  padding: 1.7rem;
  font-size: 1.2rem;
  box-shadow: 0px 0px 5px gray;
  gap: 10px;
  width: fit-content;
}

.review-author {
  font-weight: bold;
  font-size: 1.5rem;
}

.review-rating {
  color: orange;
}

.review-text {
  margin-top: 0.5rem;
}

.reviews-footer {
  margin-top: 2rem;
}

.reviews-button {
  width: 20vw;
  height: 3rem;
  border-radius: 10px;
  border: none;
  font-family: Comfortaa, sans-serif;
  font-weight: 500;
  font-size: 1.4rem;
  cursor: pointer;
  transition: 0.3s ease;
  background-color: #007bff;
  color: #fff;
}
.reviews-button:hover {
  background: #0056b3;
}
@media (max-width: 946px) {
  .reviews-main-container {
    margin: 2rem;
  }
  .reviews-header {
    font-size: 2rem;
  }
  .reviews-button {
    font-size: 1rem;
  }
  .review-card {
    font-size: 1rem;
  }
  .reviews-list {
    flex-direction: column;
  }
}
@media (max-width: 601px) {
  .reviews-button {
    width: 30vw;
  }
  .review-card {
    font-size: 0.8rem;
  }
  .review-author {
    font-size: 1.1rem;
  }
}
@media (max-width: 393px) {
  .reviews-button {
    width: 50vw;
  }
}
@media (max-width: 1369px) {
  .review-card {
    font-size: 0.9rem;
  }
}
